  <template>
      <div class="container">
        <div ref="container" class="ball-container"></div>
        
        <div id="main-title" title="𝓗𝓮𝓵𝓵𝓸 𝓣𝓱𝓮𝓻𝓮!💻">𝓗𝓮𝓵𝓵𝓸 𝓣𝓱𝓮𝓻𝓮!💻</div> 

        <div class="options">
          <button class="button" role="button" onclick="window.location.href='#/projects';">𝐏𝐫𝐨𝐣𝐞𝐜𝐭𝐬</button>
          <button class="button" role="button" onclick="window.location.href='#/aboutme';">𝐀𝐛𝐨𝐮𝐭 𝐦𝐞</button>
          <button class="button" role="button" onclick="window.location.href='#/talents';">𝐓𝐚𝐥𝐞𝐧𝐭𝐬</button>
        </div>
        
      </div>
    </template>
    
    <script>
    import * as THREE from 'three';
    import basketballTexture from '@/assets/basketball-texture.png';
    
    export default {
      name: 'Ball',
      mounted() {
        // Create a Three.js scene
        this.scene = new THREE.Scene();
    
        // Create a Three.js camera
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        this.camera.position.z = 5;
    
        // Create a Three.js renderer
        this.renderer = new THREE.WebGLRenderer();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.$refs.container.appendChild(this.renderer.domElement);
    
        // Load the basketball texture
        const textureLoader = new THREE.TextureLoader();
        const texture = textureLoader.load(basketballTexture);
    
        // Create a material with the basketball texture and blue color
        const material = new THREE.MeshPhongMaterial({ map: texture, color: 0xDC7C31 }); // Blue color
    
        // Create a sphere geometry
        const geometry = new THREE.SphereGeometry(1, 20, 20);
    
        // Create a mesh with the sphere geometry and basketball texture material
        this.ball = new THREE.Mesh(geometry, material);
    
        // Add the mesh to the scene
        this.scene.add(this.ball);
    
        // Add lights to the scene for shading
        const ambientLight = new THREE.AmbientLight(0xffffff, 0.5); // Soft white light
        this.scene.add(ambientLight);
    
        const directionalLight = new THREE.DirectionalLight(0xffffff, 0.7); // White directional light
        directionalLight.position.set(1, 1, 1).normalize();
        this.scene.add(directionalLight);
    
        // Render loop
        const animate = () => {
          requestAnimationFrame(animate);
    
          this.ball.rotation.x += 0.02;
          this.ball.rotation.y += 0.02;
    
          // Render the scene
          this.renderer.render(this.scene, this.camera);
        };
        animate();
      },
      beforeDestroy() {
        // Cleans up the Three.js resources when the component is destroyed
        this.renderer.dispose();
        this.scene.dispose();
      }
    };
    </script>
    
    <style scoped>
    .container {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100vh;
    }
    
    .ball-container {
      max-width: 100%;
      height: 66%;
    }
    
    .options {
      display: flex;
      max-width: 100%;
      justify-content: center;
      padding: 5px;
    }
    
    .button,
    .button:after {
      height: 10vh;
      line-height: 10vh;
      font-size: 5vw;
      font-family: "Ubuntu", sans-serif;
      background: linear-gradient(45deg, transparent 10%, lawngreen 5%);
      border: 0;
      color: #ffffff;
      box-shadow: 2vw 0vw 0vw #21c704;
      outline: transparent;
      position: relative;
      user-select: none;
      -webkit-user-select: none;
      touch-action: manipulation;
      cursor: pointer;
      display: flex;
      justify-content: center;
      margin-right: 1vw;
    }
    
    .button:after {
      --slice-0: inset(50% 50% 50% 50%);
      --slice-1: inset(80% -6px 0 0);
      --slice-2: inset(50% -6px 30% 0);
      --slice-3: inset(10% -6px 85% 0);
      --slice-4: inset(40% -6px 43% 0);
      --slice-5: inset(80% -6px 5% 0);
    
      content: 'Discover More';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(45deg, transparent 3%, #00E6F6 3%, #00E6F6 5%, lawngreen 5%);
      text-shadow: -3px -3px 0px #F8F005, 3px 3px 0px #00E6F6;
      clip-path: var(--slice-0);
    }
    
    .button:hover:after {
      animation: 1s glitch;
      animation-timing-function: steps(2, end);
    }
    
    @keyframes glitch {
      0% {
        clip-path: var(--slice-1);
        transform: translate(-1vw, -0.5vh); 
      }
      10% {
        clip-path: var(--slice-3);
        transform: translate(0.5vw, 0.5vh); 
      }
      20% {
        clip-path: var(--slice-1);
        transform: translate(-0.5vw, 0.5vh); 
      }
      30% {
        clip-path: var(--slice-3);
        transform: translate(0vw, 0.25vh); 
      }
      40% {
        clip-path: var(--slice-2);
        transform: translate(-0.25vw, 0vh); 
      }
      50% {
        clip-path: var(--slice-3);
        transform: translate(0.25vw, 0vh); 
      }
      60% {
        clip-path: var(--slice-4);
        transform: translate(0.25vw, 0.5vh); 
      }
      70% {
        clip-path: var(--slice-2);
        transform: translate(-0.5vw, 0.5vh); 
      }
      80% {
        clip-path: var(--slice-5);
        transform: translate(1vw, -0.5vh);
      }
      90% {
        clip-path: var(--slice-1);
        transform: translate(-0.5vw, 0vh);
      }
      100% {
        clip-path: var(--slice-1);
        transform: translate(0);
      }
    }
    
    @media (min-width: 768px) {
      .button,
      .button:after {
        width: 20vw;
        height: 8vh;
        line-height: 8vh;
        font-size: 1.5vw;
        box-shadow: 1.5vw 0px 0px #21c704;
      }
    }

  /* //////////////////////////////////////////////////////////////////////////// */
  
  #main-title{
    margin-top: 0.5vw;
    font-size: 4.5vw;
    color: lawngreen;
    animation: glitch2 2s linear infinite;

  }

  @keyframes glitch2{
    2%,64%{
      transform: translate(0.2vw,0) skew(0deg); 
    }
    5%,60%{
      transform: translate(-0.2vw,0) skew(0deg); 
    }
    62%{
      transform: translate(0,0) skew(0.2deg); 
    }
  }

  #main-title:before,
  #main-title:after{
    content: attr(title);
    position: absolute;
    left: 0;
  }

  #main-title:before{
    animation: glitchTop 1s linear infinite;
    clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
  }

  @keyframes glitchTop{
    2%,64%{
      transform: translate(0.2vw,-0.2vw); 
    }
    4%,60%{
      transform: translate(-0.2vw,0.2vw); 
    }
    62%{
      transform: translate(1.3vw,-0.1vw) skew(-0.13deg); 
    }
  }

  #main-title:after{
    animation: glitchBotom 1.5s linear infinite;
    clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
    -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  }

  @keyframes glitchBotom{
    2%,64%{
      transform: translate(-0.2vw,0); 
    }
    4%,60%{
      transform: translate(-0.2vw,0); 
    }
    62%{
      transform: translate(-2.2vw,0.5vw) skew(21deg); 
    }
  }

  @media only screen and (max-width: 768px){
    html,
    body{
    max-width: 100%;
    overflow-x: hidden;
    }
  }
</style>
    